'use client';  // IMPORTANT: Add this at the top for Client Components
import { useState } from 'react';
import { useRouter } from 'next/router';

export default function Dashboard() {
  const [events, setEvents] = useState([]);
  const router = useRouter();

  const createEvent = async () => {
    const eventName = `Event-${Date.now()}`;

    // Call API to upload event to S3
    const res = await fetch('/api/create-event', {
      method: 'POST',
      body: JSON.stringify({ eventName }),
      headers: { 'Content-Type': 'application/json' },
    });

    if (res.ok) {
      setEvents([...events, eventName]);
    }
  };

  const goToEvent = (eventName) => {
    router.push(`/event/${eventName}`);
  };

  return (
    <div className="p-5">
      <button onClick={createEvent} className="bg-blue-500 text-white px-4 py-2 rounded">
        Create Main Event
      </button>

      <div className="mt-5">
        {events.map((event) => (
          <div key={event} className="cursor-pointer" onClick={() => goToEvent(event)}>
            {event}
          </div>
        ))}
      </div>
    </div>
  );
}
